.app-wrapper {
  width: 1170px;
  margin: 0 auto;
  display: grid;
  grid-template-areas: "header header " "nav main-content";
  grid-template-rows: 60px 1fr;
  grid-template-columns: 20% 1fr;
  grid-gap: 10px;
}

.app-wrapper-content {
  grid-area: main-content;
  background-color: rgb(88, 88, 46);
}
